
const initialStore = {
  state: {},
  mutations: {},
  actions: {},
  getters: {}
}

export const crudStore = (
  crudModules = [],
  defaultModule = initialStore
) => {
  crudModules.forEach(item => {
    const storeKeys = [
      'state',
      'mutations',
      'actions',
      'getters'
    ]
    storeKeys.forEach(key => {
      if (item[key]) {
        defaultModule[key] = {
          ...defaultModule[key],
          ...item[key]
        }
      }
    })
  })
  return defaultModule
}
